					commentJSON.id += '-' + index;
					commentJSON.content = '';
					commentJSON.file = file;
					commentJSON.fileURL = 'C:/fakepath/' + file.name;
					commentJSON.fileMimeType = file.type;

					commentJSON = self.applyExternalMappings(commentJSON);
